import { gallerySwiperInit } from "./helpers";

document.addEventListener('DOMContentLoaded', () => {
    const singleProduct = document.querySelector('.wcl-single-product');

    if ( singleProduct ) {
        const topSwiper = document.querySelector('.topSwiper');
        const thumbsSwiper = document.querySelector('.thumbsSwiper');

        if ( topSwiper && thumbsSwiper ) {
            const swiper = new Swiper(thumbsSwiper, {
                spaceBetween: 15,
                slidesPerView: 4,
                freeMode: true,
                watchSlidesProgress: true,
                grabCursor: true,
                breakpoints: {
                    320: {
                        slidesPerView: 3,
                    },
                    576: {
                        slidesPerView: 4,
                    },
                }
            });

            const swiper2 = new Swiper(topSwiper, {
                spaceBetween: 10,
                grabCursor: true,
                navigation: {
                    nextEl: '.swiper-button-next',
                    prevEl: '.swiper-button-prev',
                },
                thumbs: {
                    swiper: swiper,
                },
            });

            Fancybox.bind('[data-fancybox]', {
                Carousel: {
                    transition: 'slide'
                }
            });
        }
    }

    const catalog = document.querySelector('.wcl-catalog');

    if ( catalog ) {
        gallerySwiperInit();
    }
});