document.addEventListener('DOMContentLoaded', () => {
    const catalog = document.querySelector('.wcl-catalog');

    if ( catalog ) {
        catalog.addEventListener('click', e => {
            const link = e.target.closest('.wcl-catalog-item__link');

            if ( link ) {
                const isInteractive = e.target.closest('[data-interactive="true"]');

                if ( isInteractive ) {
                    e.preventDefault();
                    return;
                }
            }
        });
    }
});