import { selectedCurrency, wclMessageTemplates, wclT } from "./helpers";

document.addEventListener('DOMContentLoaded', () => {
    const root = document.documentElement;

    const accentColor = getComputedStyle(root).getPropertyValue('--accent-color').trim();
    const redColor = getComputedStyle(root).getPropertyValue('--red-color').trim();


    document.addEventListener('click', e => {
        if ( e.target.matches('.wcl-make-offer') ) {
            e.preventDefault();

            const button = e.target;
            const message = button.dataset.message;

            const currency = selectedCurrency();

            Swal.fire({
                title: wclT('modal.makeOffer.title'),
                text: wclT('modal.makeOffer.text', currency),
                input: 'number',
                icon: 'question',
                showCancelButton: true,
                confirmButtonColor: accentColor,
                cancelButtonColor: redColor,
                confirmButtonText: wclT('modal.makeOffer.button'),
                cancelButtonText: wclT('modal.cancel'),
                preConfirm: ( value ) => {
                    if ( !value || value.trim() === '' || value < 1 ) {
                        Swal.showValidationMessage(wclT('modal.makeOffer.error'));
                        return false;
                    }
                    return value;
                }
            }).then(( result ) => {
                if ( result.isConfirmed ) {
                    const offer = result.value;

                    const placeholders = {
                        offer: `${offer} (${currency})`,
                    };

                    const phone = config.contact_phone
                    const newMessage = wclMessageTemplates(message, placeholders);
                    const link = `https://wa.me/${phone}?text=${encodeURIComponent(newMessage)}`;

                    window.open(link, '_blank');
                }
            });
        }
    }, false);
});