import { wclFetch, wclT } from "./helpers";

document.addEventListener('DOMContentLoaded', () => {
    const makeSelect = document.querySelector('.wcl-select select[name="make"]');
    const modelSelect = document.querySelector('.wcl-select select[name="model"]');

    if ( makeSelect && modelSelect ) {
        makeSelect.addEventListener('change', async e => {
            modelSelect.innerHTML = `<option value="0">${wclT('filters.model')}</option>`;

            const make = makeSelect.value;
            const data = new FormData();

            data.set('action', 'get_models');
            data.set('make', make);

            modelSelect.classList.add('loading');

            try {
                const response = await wclFetch(data);

                if ( !response.ok ) {
                    throw new Error(wclT('error.fetchDataError'));
                }

                const result = await response.json();

                if ( result.success == '1' ) {
                    modelSelect.innerHTML = result.options;
                } else {
                    throw result.message;
                }
            } catch ( error ) {
                console.error(error)
            } finally {
                modelSelect.classList.remove('loading');
            }
        });
    }
});