import { wclFetch, wclT } from "./helpers";

document.addEventListener('DOMContentLoaded', () => {
    const currencySelect = document.querySelector('.wcl-user-currency select');

    if ( currencySelect ) {
        currencySelect.addEventListener('change', async e => {
            e.preventDefault();

            if ( currencySelect.classList.contains('loading') ) {
                return;
            }

            // Add 'loading' class to not allow trigger another query until this one is finished
            currencySelect.classList.add('loading');

            const data = new FormData();
            data.set('action', 'update_user_currency');
            data.set('currency', currencySelect.value);

            try {
                const response = await wclFetch(data);

                if ( !response.ok ) {
                    throw new Error(wclT('error.fetchDataError'));
                }

                const result = await response.json();

                if ( result.success == '1' ) {
                    document.location.reload();
                } else {
                    throw result.message;
                }
            } catch ( error ) {
                const root = document.documentElement;
                const accentColor = getComputedStyle(root).getPropertyValue('--accent-color').trim();

                Swal.fire({
                    title: wclT('modal.error.title'),
                    text: wclT('modal.error.text'),
                    icon: 'error',
                    confirmButtonColor: accentColor,
                });

                console.error(error);
            } finally {
                // Remove "loading" class
                currencySelect.classList.remove('loading');
            }
        });
    }
});