import { wclFetch, wclT } from "./helpers";

document.addEventListener('DOMContentLoaded', () => {
    const form = document.getElementById('wcl-change-password');

    if ( form ) {
        const validation = new window.JustValidate(form);

        validation
            .addField('input[name="current_password"]', [
                {
                    rule: 'required',
                    errorMessage: wclT('validation.required'),
                },
                {
                    rule: 'minLength',
                    value: 8,
                    errorMessage: wclT('validation.minLength', 8),
                },
                {
                    rule: 'password',
                    errorMessage: wclT('validation.password'),
                },
            ])
            .addField('input[name="new_password"]', [
                {
                    rule: 'required',
                    errorMessage: wclT('validation.required'),
                },
                {
                    rule: 'minLength',
                    value: 8,
                    errorMessage: wclT('validation.minLength', 8),
                },
                {
                    rule: 'password',
                    errorMessage: wclT('validation.password'),
                },
            ])
            .addField('input[name="confirm_password"]', [
                {
                    rule: 'required',
                    errorMessage: wclT('validation.required'),
                },
                {
                    rule: 'minLength',
                    value: 8,
                    errorMessage: wclT('validation.minLength', 8),
                },
                {
                    rule: 'password',
                    errorMessage: wclT('validation.password'),
                },
                {
                    validator: ( value, fields ) => {
                        if (
                            fields['input[name="new_password"]'] &&
                            fields['input[name="new_password"]'].elem
                        ) {
                            const repeatPasswordValue =
                                fields['input[name="new_password"]'].elem.value;

                            return value === repeatPasswordValue;
                        }

                        return true;
                    },
                    errorMessage: wclT('validation.passwordSame'),
                },
            ])
            .onSuccess(async ( event ) => {
                event.preventDefault();

                const message = form.querySelector('.wcl-message');

                message.classList.remove('show', 'error', 'success');

                const data = new FormData(form);

                data.set('action', 'change_password');

                form.classList.add('loading');

                try {
                    const response = await wclFetch(data);

                    if ( !response.ok ) {
                        throw new Error(wclT('error.fetchDataError'));
                    }

                    const result = await response.json();

                    if ( result.success == '1' ) {
                        message.innerHTML = result.message;
                        message.classList.add('show', 'success');
                    } else {
                        throw result.message;
                    }
                } catch ( error ) {
                    message.innerHTML = error;
                    message.classList.add('show', 'error');
                } finally {
                    form.classList.remove('loading');
                }
            });
    }
});