import { readCookie, wclFetch, wclT } from "./helpers";

document.addEventListener('DOMContentLoaded', () => {
    const root = document.documentElement;

    const accentColor = getComputedStyle(root).getPropertyValue('--accent-color').trim();

    const form = document.getElementById('wcl-register-form');

    if ( form ) {
        const validation = new window.JustValidate(form);

        validation
            .addField('input[name="registered_business_name"]', [
                {
                    rule: 'required',
                    errorMessage: wclT('validation.required'),
                },
            ])
            .addField('input[name="business_address"]', [
                {
                    rule: 'required',
                    errorMessage: wclT('validation.required'),
                },
            ])
            .addField('input[name="type_of_business"]', [
                {
                    rule: 'required',
                    errorMessage: wclT('validation.required'),
                },
            ])
            .addField('input[name="business_registration_number"]', [
                {
                    rule: 'required',
                    errorMessage: wclT('validation.required'),
                },
            ])
            .addField('input[name="vat_number"]', [
                {
                    rule: 'required',
                    errorMessage: wclT('validation.required'),
                },
            ])
            .addField('input[name="primary_contact_person"]', [
                {
                    rule: 'required',
                    errorMessage: wclT('validation.required'),
                },
            ])
            .addField('input[name="position"]', [
                {
                    rule: 'required',
                    errorMessage: wclT('validation.required'),
                },
            ])
            .addField('input[name="phone_number"]', [
                {
                    rule: 'required',
                    errorMessage: wclT('validation.required'),
                },
                {
                    validator: ( value ) => {
                        const phoneRegex = /^\+?[1-9]\d{1,14}$/;
                        const sanitizedValue = value.replace(/\s+/g, '');
                        return phoneRegex.test(sanitizedValue);
                    },
                    errorMessage: wclT('validation.phone'),
                },
            ])
            .addField('input[name="email"]', [
                {
                    rule: 'required',
                    errorMessage: wclT('validation.required'),
                },
                {
                    rule: 'email',
                    errorMessage: wclT('validation.email'),
                },
            ])
            .addField('input[name="password"]', [
                {
                    rule: 'required',
                    errorMessage: wclT('validation.required'),
                },
                {
                    rule: 'minLength',
                    value: 8,
                    errorMessage: wclT('validation.minLength', 8),
                },
                {
                    rule: 'password',
                    errorMessage: wclT('validation.password'),
                },
            ])
            .onSuccess(async ( event ) => {
                event.preventDefault();

                const message = form.querySelector('.wcl-message');

                message.classList.remove('show', 'error', 'success');

                const data = new FormData(form);

                // Check current language
                const lang = readCookie('wp-wpml_current_language') ?? 'en';

                data.set('language', lang);
                data.set('action', 'registration');

                form.classList.add('loading');

                try {
                    const response = await wclFetch(data);

                    if ( !response.ok ) {
                        throw new Error(wclT('error.fetchDataError'));
                    }

                    const result = await response.json();

                    if ( result.success == '1' ) {
                        form.reset();

                        Swal.fire({
                            title: result.modal_subject,
                            html: result.modal_message,
                            icon: 'success',
                            confirmButtonColor: accentColor
                        });
                    } else {
                        throw result.message;
                    }
                } catch ( error ) {
                    message.innerHTML = error;
                    message.classList.add('show', 'error');
                } finally {
                    form.classList.remove('loading');
                }
            });
    }
});

