import { wclFetch, wclT } from "./helpers";

document.addEventListener('DOMContentLoaded', () => {
    const root = document.documentElement;

    const accentColor = getComputedStyle(root).getPropertyValue('--accent-color').trim();
    const redColor = getComputedStyle(root).getPropertyValue('--red-color').trim();


    document.addEventListener('click', e => {
        if ( e.target.matches('.wcl-reserve-car') ) {
            e.preventDefault();

            const button = e.target;
            const postId = button.dataset.id;

            const data = new FormData();
            data.set('action', 'reserve_car');
            data.set('post_id', postId);

            Swal.fire({
                title: wclT('modal.reserveCar.title'),
                text: wclT('modal.reserveCar.text'),
                icon: 'question',
                showCancelButton: true,
                confirmButtonColor: accentColor,
                cancelButtonColor: redColor,
                confirmButtonText: wclT('modal.reserveCar.button'),
                cancelButtonText: wclT('modal.cancel'),
                showLoaderOnConfirm: true,
                preConfirm: async () => {
                    try {
                        const response = await wclFetch(data);

                        if ( !response.ok ) {
                            throw new Error(wclT('error.fetchDataError'));
                        }

                        const result = await response.json();

                        if ( result.success !== 1 ) {
                            throw result.message || wclT('error.unknownError');
                        }
                    } catch ( error ) {
                        Swal.showValidationMessage(error);
                    }
                },
                allowOutsideClick: () => !Swal.isLoading()
            }).then(( result ) => {
                if ( result.isConfirmed ) {
                    Swal.fire({
                        title: wclT('modal.thanks.title'),
                        text: wclT('modal.thanks.text'),
                        icon: 'success',
                        confirmButtonColor: accentColor,
                    }).then(() => {
                        document.location.reload();
                    });
                }
            });
        }
    }, false);
});