import { checkBottom, gallerySwiperInit, wclFetch, wclT } from "./helpers";

document.addEventListener('DOMContentLoaded', () => {
    const catalog = document.querySelector('.wcl-catalog__list');
    const preloader = document.querySelector('.wcl-preloader');

    let loading = false;

    if ( catalog ) {
        window.addEventListener('scroll', async () => {
            let currentPage = Number(catalog.dataset.page);
            let loadMore = Number(catalog.dataset.loadMore);

            if ( checkBottom(catalog) && loadMore && !loading ) {
                // Loading posts running variable to not run multiple AJAX at once (only one by one as user scroll)
                loading = true;

                // Add 'loading' class to not allow trigger another query until this one is finished
                preloader.classList.add('loading');

                const data = new FormData();
                data.set('action', 'load_more');
                data.set('page', currentPage + 1);

                const params = new URLSearchParams(window.location.search);

                params.forEach(( value, key ) => {
                    data.set(key, value);
                });

                try {
                    const response = await wclFetch(data);

                    if ( !response.ok ) {
                        throw new Error(wclT('error.fetchDataError'));
                    }

                    const result = await response.json();

                    if ( result.success == '1' ) {
                        if ( !result.post_markup ) {
                            return
                        }

                        catalog.insertAdjacentHTML('beforeend', result.post_markup);

                        gallerySwiperInit();

                        currentPage++;
                        catalog.dataset.page = currentPage;

                        // Pagination check
                        loadMore = currentPage >= result.max_num_pages ? false : loadMore;
                        catalog.dataset.loadMore = loadMore ? '1' : '0';
                    } else {
                        throw result.message;
                    }
                } catch ( error ) {
                    console.error(error);
                } finally {
                    loading = false;

                    // Remove 'loading' class
                    preloader.classList.remove('loading');
                }
            }
        });
    }
});