(function () {
    const productPage = document.querySelector('.wcl-single-product');

    if ( productPage ) {
        const postId = productPage.dataset.postId;

        const websocketUrl = config.websocket_url;
        const ws = new WebSocket(websocketUrl);

        ws.onopen = () => {
            ws.send(JSON.stringify({ type: 'join', postId }));
        }

        ws.onmessage = ( event ) => {
            const data = JSON.parse(event.data);

            if ( data.type === 'updateCount' && postId === data.postId ) {
                const counterElement = document.getElementById('active-users-counter');

                if ( counterElement ) {
                    counterElement.textContent = data.count;
                }
            }
        }

        ws.onclose = () => {
            console.log('Connection to the WebSocket server closed');
        }

        ws.onerror = ( error ) => {
            console.error('WebSocket Error:', error);
        }
    }
})();