import { gallerySwiperInit, updateURLParameters, wclFetch, wclT } from "./helpers";

document.addEventListener('DOMContentLoaded', () => {
    const catalog = document.querySelector('.wcl-catalog__list');
    const filtersList = document.querySelectorAll('.wcl-filter');

    if ( catalog && filtersList.length > 0 ) {
        const data = new FormData();
        data.set('action', 'catalog_filters');

        const params = new URLSearchParams(window.location.search);

        params.forEach(( value, key ) => {
            data.set(key, value);
        });

        filtersList.forEach(filter => {
            filter.addEventListener('change', async e => {
                const name = filter.name;
                const value = filter.value;

                updateURLParameters({ [name]: value });

                if ( value && value !== '0' ) {
                    if ( name === 'make' ) {
                        data.delete('model');
                    }

                    data.set(name, value);
                } else {
                    data.delete(name)
                }

                try {
                    const response = await wclFetch(data);

                    if ( !response.ok ) {
                        throw new Error(wclT('error.fetchDataError'));
                    }

                    const result = await response.json();

                    if ( result.success == '1' ) {
                        catalog.innerHTML = result.post_markup;

                        gallerySwiperInit();

                        catalog.dataset.page = '1';
                        catalog.dataset.loadMore = result.load_more ? '1' : '0';
                    } else {
                        throw result.message;
                    }
                } catch ( error ) {
                    console.error(error);
                } finally {
                    // TODO: Hide preloader
                }
            });
        });
    }
});