document.addEventListener('DOMContentLoaded', () => {
    const accountMenu = document.querySelectorAll('.wcl-account__menu button');
    const accountTabs = document.querySelectorAll('.wcl-account__tab');

    if ( accountMenu.length && accountTabs ) {
        accountMenu.forEach(item => {
            item.addEventListener('click', e => {
                e.preventDefault();

                accountMenu.forEach(menuItem => {
                    menuItem.classList.remove('active');
                });

                item.classList.add('active');

                const tab = item.dataset.tab;

                accountTabs.forEach(tabItem => {
                    tabItem.classList.remove('active');
                });

                const targetTab = Array.from(accountTabs).find(tabItem => tabItem.dataset.tabContent === tab);

                if ( targetTab ) {
                    targetTab.classList.add('active');

                    const messages = document.querySelectorAll('.wcl-message');

                    if ( messages.length ) {
                        messages.forEach(message => {
                            message.classList.remove('show', 'error', 'success');
                        })
                    }

                    const currentUrl = new URL(window.location.href);
                    currentUrl.searchParams.set('tab', tab);
                    history.pushState({}, '', currentUrl);
                }
            })
        })
    }
});